html,
body,
#root {
  height: 100%;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.body .title {
  font-size: 96px;
  font-family: 'bradhitc', 'cnhw';
  font-weight: 900;
}

.body .controls {
  width: 100%;
  text-align: center;
  margin: 20px;
}

.body .editor {
  /* width: 80%; */
  width: 1152px;
  background-color: #282a36;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.55) 0px 20px 68px;
  padding: 24px;
}

.body .show-card {
  width: 1152px;
  margin: 70px 0;
  padding: 24px;
  border-radius: 5px;
  /* min-height: 400px; */
  max-height: 1500px;
  height: auto;
  box-shadow: rgba(0, 0, 0, 0.55) 0px 20px 68px !important;
}

.preview {
  text-align: center;
  background: #fff;
  padding: 10px;
  min-width: 100%;
  height: auto;
  overflow: scroll;
}

.preview svg text {
  font-family: 'bradhitc', 'cnhw'!important;
  font-weight: 700;
}
